import {
    getAllRoom,
    addRoom,
    delRoom
} from '../../service/questManger'

export default {
    namespace:'room',
    state:{
        allRoom: []
    },
    effects: {
        *getAllRoom(_, {put, call}) {
            let res = yield call(getAllRoom);
            yield put({
                type: 'saveAllRoom',
                payload: res.data.filter(val => val.room_text !== 'null')
            })
        },
        *addRoom({ payload }, {put, call}) {
            yield call(addRoom, payload);
            yield put({
                type: 'getAllRoom'
            })
        },
        *delRoom({ payload }, {put, call}) {
            yield call(delRoom, payload);
            yield put({
                type: 'getAllRoom'
            })
        }
    },
    reducers: {
        saveAllRoom(state, {payload}) {
            return {
                ...state,
                allRoom: payload
            }
        }
    }
}